html, body {
  height: 100%;
}

:root {
  --bg: #f4f7f9;
  --card-bg: #fff;
  --text: #1f2a37;
  --muted: #555;
  --accent: #0056b3;
  --muted-border: #e9ecef;
  --shadow: 0 2px 8px #0f172a0f;
}

[data-theme="dark"] {
  --bg: #0b0f14;
  --card-bg: #0f1418;
  --text: #e6eef9;
  --muted: #9aa6b2;
  --accent: #4da3ff;
  --muted-border: #ffffff0f;
  --shadow: 0 2px 12px #0009;
}

body {
  background-color: var(--bg);
  color: var(--text);
  box-sizing: border-box;
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  overflow-y: auto;
}

#presentation {
  text-align: center;
}

.content, main.content {
  --site-header-height: 80px;
  padding: var(--site-header-height) 20px 20px 20px;
  margin: 0;
}

.content.hero-page {
  padding-top: 0;
}

h1 {
  color: var(--text);
  letter-spacing: -.015em;
  opacity: .95;
  margin: 0 0 .5em;
  font-family: Montserrat, sans-serif;
  font-size: clamp(2.5em, 5vw, 3.2em);
  font-weight: 600;
}

h2 {
  color: var(--text);
  margin-top: 1.5em;
  margin-bottom: .75em;
  font-size: 1.5em;
  font-weight: 600;
}

h3 {
  color: var(--text);
  margin: 1.25em 0 .5em;
  font-size: 1.1em;
}

ul {
  margin: 0 0 1em;
  padding-left: 20px;
  list-style-type: disc;
}

ul li {
  margin-bottom: .5em;
  font-size: 1em;
}

.button-container {
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.button {
  border: 2px solid var(--accent);
  color: var(--accent);
  cursor: pointer;
  background-color: #0000;
  border-radius: 5px;
  padding: 10px 20px;
  text-decoration: none;
  transition: background-color .3s, color .3s, border-color .3s;
}

.button:hover {
  background-color: var(--accent);
  color: var(--card-bg);
}

.hero {
  height: calc(100vh - var(--site-header-height));
  text-align: center;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

@media (width <= 600px) {
  .content, main.content {
    --site-header-height: 64px;
  }

  .content.hero-page {
    padding-top: 0;
  }

  .hero {
    height: 70vh;
  }

  .button-container {
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
    display: flex;
  }
}
